/* brian design css */

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'PT Sans', sans-serif;
  }
  
  .about-me,
  .projects,
  .contact{
    display: flex;
    height: 90vh;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
  }
  
  .projects {
    background-image: "https://i.pinimg.com/originals/4d/e4/cd/4de4cd4f761295859ae0e372cdeddd73.jpg";
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    color: #fff;
    font-size: 100px;
  }
  
  .contact {
    background-image: "https://i.pinimg.com/originals/4d/e4/cd/4de4cd4f761295859ae0e372cdeddd73.jpg";
    background-position: center;
    background-size: fill;
    background-repeat: no-repeat;
    color: #fff;
    font-size: 100px;
  }
  